/* Animation */
.right {
  -webkit-animation: slideFromRight 1s ease-in-out forwards;
          animation: slideFromRight 1s ease-in-out forwards;
}

.left {
  -webkit-animation: slideFromLeft 1s ease-in-out forwards;
          animation: slideFromLeft 1s ease-in-out forwards;
}

@-webkit-keyframes slideFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }

  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes slideFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }

  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@-webkit-keyframes slideFromRight {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }

  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes slideFromRight {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }

  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}


/* Animation */

:root {
    --main-web-color: #00CEC8;
    --light-color: #f5f5f5;
    --dark-color: #1b242c;
    --txtColor: #111;
    --secTxtColor: #666;
    --white: #FFF;
}

/* Sidebar */
.custom-toggle::after {
    display: none !important;
}

.dropdown-menu::before {
    content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    top: -10px;
    left: 50%;
    margin-left: -10px;
    border-style: outset;
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent #4f5d73 transparent;
}

.dropdown-menu.show {
    display: block;
    background: #1b242c;
    background: var(--dark-color);
    border-radius: 10px;
    margin-top: 17px;
    text-align: center;
    padding: 10px;
}

.dropdown-item {
    color: #FFF !important;
    color: var(--white) !important;
    transition: .5s;

}

.dropdown-item:focus,
.dropdown-item:hover {
    background-color: #00CEC8 !important;
    background-color: var(--main-web-color) !important;
    border-radius: 10px;
}

.btn {
    /* background: transparent; */
    /* border: 0;
    padding: 0; */
    cursor: pointer;
}

.sidebar {
    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 72px;
    height: 100%;
    background: #0c8884;
    transition: width 0.4s;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
}

body.open .sidebar {
    width: 260px;
}

.sidebar-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 300px;
    height: inherit;
    display: flex;
    flex-direction: column;
    padding-bottom: 10px;

}

.sidebar-header {
    display: flex;
    align-items: center;
    height: 72px;
    padding: 0 1.25rem 0 0;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
}


#light .sidebar-nav {
    background-color: #0c8884;
}

#dark .sidebar-nav {
    background-color: #1b242c;
    background-color: var(--dark-color);
}

#light .profile-name,
#light .a {
    color: #1b242c;
    color: var(--dark-color);
}

#dark .profile-name,
#dark .a {
    color: #FFF;
    color: var(--white);
}

.profile-name {
    font-weight: bold;
}

.end {
    display: flex;
    justify-content: end;
}

/* Header */
.sidebar-header .navlinks {
    display: flex;
    margin-bottom: 0;
    margin-right: 10%;
}

.navlinks li {
    list-style: none;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Poppins";
    /* animation: bounceInDown 0.5s ease-in-out forwards; */
}

nav .navlinks li .a {
    text-decoration: none;
    font-weight: 300;
    transition: .5s;
    padding: 10px;
    cursor: pointer;
}

/* Header */
.sidebar-burger {
    width: 72px;
    height: 72px;
    display: grid;
    place-items: center;
    background: url(/static/media/icon-burger.37f1fa02.svg) no-repeat center center;
    transition: 0.3s;
    cursor: pointer;
}

body.open .sidebar-burger {
    background: url(/static/media/icon-close.c17eb1f0.svg) no-repeat center center;
}

.sidebar-logo {
    height: 35px;
    opacity: 0;
    transition: 0.3s;
}

body.open .sidebar-logo {
    opacity: 1;
}

.sidebar-nav {
    padding-top: 10px;
    flex: 1 1 auto;
}

.sidebar .btn {
    display: flex;
    grid-gap: 25px;
    gap: 25px;
    align-items: center;
    height: 50px;
    width: 72px;
    font-family: "Poppins";
    font-size: 16px;
    font-weight: 200;
    letter-spacing: 2px;
    line-height: 1;
    padding: 0 25px;
    transition: 0.5s;

}

.sidebar-nav .btn:hover,
.sidebar-foot .btn:hover {
    background-color: #0A6D6E;
    color: #FFF;
    color: var(--white);
}

.sidebar .btn>img {
    width: 24px;
    height: 24px;
}

.sidebar .btn>h5 {
    color: #f9f9f9;
    opacity: 0;
    transition: 0.3s;
}

.spin {
    color: #f9f9f9;
    font-size: 18px;
    /* font-weight: bold; */
    margin-left: 10px;
    cursor: default;
    font-family: "Poppins";

}

@-webkit-keyframes appear {
    0% {
        opacity: 0;
        translate: 0 10px;
    }

    100% {
        opacity: 1;
        translate: 0;
    }
}

@keyframes appear {
    0% {
        opacity: 0;
        translate: 0 10px;
    }

    100% {
        opacity: 1;
        translate: 0;
    }
}

body.open .sidebar .btn>h5 {
    opacity: 1;
    -webkit-animation: appear 0.3s both;
            animation: appear 0.3s both;
}

@media (max-width: 767.98px) {
    .profile-name {
        display: none;
