/* Login Btn */
.login-btn {
    --secondary-color: #fff;
    --hover-color: #0caaa6;
    --arrow-width: 10px;
    --arrow-stroke: 2px;
    box-sizing: border-box;
    border: 0;
    border-radius: 20px;
    color: var(--secondary-color);
    padding: 8px 1.8em !important;
    background: #00CEC8;
    background: var(--main-web-color);
    display: flex;
    transition: 0.2s background;
    align-items: center;
    grid-gap: 0.6em;
    gap: 0.6em;
    font-weight: bold;
    cursor: pointer;
    -webkit-animation: moving-arrow 0.6s linear infinite;
            animation: moving-arrow 0.6s linear infinite;

}

.login-btn .arrow-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-btn .arrow {
    margin-top: 1px;
    width: var(--arrow-width);
    background: #00CEC8;
    background: var(--main-web-color);
    height: var(--arrow-stroke);
    position: relative;
    transition: 0.2s;
}

.login-btn .arrow::before {
    content: "";
    box-sizing: border-box;
    position: absolute;
    border: solid var(--secondary-color);
    border-width: 0 var(--arrow-stroke) var(--arrow-stroke) 0;
    display: inline-block;
    top: -3px;
    right: 3px;
    transition: 0.2s;
    padding: 3px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);

}

.login-btn:hover {
    background-color: var(--hover-color);
    color: var(--secondary-color);

}

.login-btn:hover .arrow {
    background: var(--secondary-color);
}

.login-btn:hover .arrow:before {
    right: 0;
}

